@import "../../scss/config";

.nav-style{
    font-family: $font-nav;
    font-size: $font-l;
    color: $black !important;
    
    a{
        // color: $black;
        margin: auto 15px;
    }
    .active{
        color: $gray !important;
        // text-decoration: underline;
    }
}
.header-wrapper{
    // padding-top: 1rem;
    padding-right: 0 !important;
    padding-left: 0 !important;
}
.nav-link{
    color: $black !important;
}
.login-wrapper{
    max-width: $main-width-phone;
    text-align: right;
    margin: 0 auto;
    padding-top: 15px;
    color: white;
}
.btn-login{
    background-color: transparent;
    border: 0;;
    &:hover{
        background-color: $theme-hover !important;
    }
    &:active {
        background-color: $theme-hover !important;
    }
    &focus{
        background-color: $theme-hover !important;
    }
}
.btn-primary.focus, .btn-primary:focus{
    background-color: $theme-hover !important;
    border-color: $theme-hover !important;
    box-shadow: none !important;
}

nav{
    max-width: $main-width-phone;
    margin: 0 auto;
    color: $black;
}
.navbar-light .navbar-toggler{
    border: 0 !important;
}
.navbar-collapse{
    background-color: $tertiary-color;
}
.navbar-nav .nav-link{
    text-align: center;
}
.nav-link:hover{
    // background-color: $theme-hover;
    color: #555;
}
.login-form{
    max-width: 375px;
    margin: 0 auto;
}
.login-btn{
    background-color: $theme-color;
    border-color: $theme-color;
    width: 100%;
}
.login-btn:hover{
    background-color: transparent !important;
    border-color: $theme-hover !important;
    color: $black;
}
.login-btn:focus{
    background-color: $theme-darker;
}
.login-btn-wrapper{
    margin: 0 auto;
    width: 100%;
    text-align: center;
}
.modal-dialog{
    font-family: 'MerriweatherSans';
}
.modal-header{
    border: 0 !important;
}
.note-text{
    color: $gray;
    font-weight: 100;
    font-size: $font-s;

}
.error-msg{
    color: red;
    font-weight: $font-s;
    font-weight: lighter;
    margin-bottom: 1rem;
}
.navbar-collapse{
    background-color: rgba(255, 255, 255, 0.9) !important;
    padding-bottom: 2rem;
    z-index: 100;
}
.click-div{
    color: $black;
    &:hover{
        cursor: pointer;
        color: #aaa;
        text-decoration: underline;
        border-radius: 4px;
    }
    
}
.title-adj{
    display: flex;
    justify-content: space-between;
    margin-bottom: 10px;
}
.modal-header{
    padding-bottom: 0 !important;
}
.custom-modal-body{
    padding-top: 0 !important;
}
.login-logo-wrapper{
    text-align: center;
    padding-bottom: 15px;
}
.form-control{
    color: #ccc;
    font-size: $font-s;
}
.modal-content{
    border: 0;
}
// .modal-header{
//     background: linear-gradient(180deg, #6e4dd0 0%, white 100%)
// }


@media (min-width: 893px){
    .navbar-collapse{
        background-color: transparent !important;
    }
}
@media (min-width: 1025px){
    .header-wrapper {
      max-width: $main-width-tablet;
    }
    .login-wrapper{
        max-width: $main-width-tablet;
    }
  }
  @media (min-width: 1250px){
    .header-wrapper{
      max-width: $main-width-pc;
    }
    .login-wrapper{
        max-width: $main-width-pc;
    }
  }